import React, { useState } from 'react';
import Modal from '../Modal';
import OnboardingModal from '../OnboardingModal';
import EditProfileModal from '../EditProfileModal';
import { motion, AnimatePresence } from 'framer-motion';
import CasePreparationSection from './CasePreparationSection';

// Add role mapping based on registration role
const roleMappings = {
  'Lawyer': ['Defense Attorney', 'Prosecutor'],
  'Prosecutor': ['Prosecutor'],
  'Defense': ['Defense Attorney'],
  'Judge': ['Judge'],
  'Clerk': ['Clerk'],
  'Observer': ['Observer', 'Witness']
};

// Add getAvailableRoles function
const getAvailableRoles = (caseObj, userRole) => {
  const baseRoles = roleMappings[userRole] || ['Observer'];
  // Filter out roles that are already taken
  const takenRoles = caseObj.agents?.map(a => a.role) || [];
  return baseRoles.filter(role => !takenRoles.includes(role));
};

export default function OverviewSection({ 
  profile, 
  activeCases, 
  uploadedCases, 
  evidence, 
  skills, 
  badges,
  showOnboarding,
  setShowOnboarding,
  handleCompleteOnboarding,
  handleSaveProfile,
  handleUploadEvidence,
  setCaseDetails,
  setJoinedCases,
  showToast,
  setUserRoles,
  addNotification,
  setCaseInteractions,
  userRoles,
  getCaseAgents,
  roleNextSteps,
  calendarEvents,
  handleAddEvent,
  handleCaseTwist,
  setShowCourtroom,
  courtroomTimeline,
  courtroomChat,
  handleCourtroomSend,
  currentSpeaker,
  setCurrentSpeaker,
  preCourtChat,
  preCourtRecipient,
  setPreCourtRecipient,
  handlePreCourtSend,
  caseInteractions,
  joinedCases = [],
  handleJoinCase,
  onUpdateRole,
  onAddInteraction,
  onAddEvidence
}) {
  const [showEditProfileModal, setShowEditProfileModal] = useState(false);
  const [caseDetails, setCaseDetailsLocal] = useState(null);
  const [showCourtroom, setShowCourtroomLocal] = useState(false);
  const [selectedCase, setSelectedCase] = useState(null);
  const [showCaseDetails, setShowCaseDetails] = useState(false);
  const [showRoleChangeModal, setShowRoleChangeModal] = useState(false);
  const [newRole, setNewRole] = useState('');
  const [activeTab, setActiveTab] = useState('timeline');
  const [showSettlementModal, setShowSettlementModal] = useState(false);
  const [settlementType, setSettlementType] = useState('mediation');
  const [settlementTerms, setSettlementTerms] = useState('');
  const [settlementAmount, setSettlementAmount] = useState('');
  const [settlementDeadline, setSettlementDeadline] = useState('');
  const [selectedParticipant, setSelectedParticipant] = useState(null);
  const [chatMessage, setChatMessage] = useState('');
  const [showChatModal, setShowChatModal] = useState(false);

  const handleSetCaseDetails = (details) => {
    setCaseDetailsLocal(details);
    if (setCaseDetails) {
      setCaseDetails(details);
    }
  };

  const handleSetShowCourtroom = (show) => {
    setShowCourtroomLocal(show);
    if (setShowCourtroom) {
      setShowCourtroom(show);
    }
  };

  const handleRoleChange = () => {
    if (newRole && selectedCase) {
      onUpdateRole(selectedCase.id, newRole);
      setShowRoleChangeModal(false);
      setNewRole('');
    }
  };

  const handleCaseClick = (caseDetails) => {
    setSelectedCase(caseDetails);
    setShowCaseDetails(true);
    handleSetCaseDetails(caseDetails);
  };

  return (
    <div className="p-4 space-y-6">
      {showOnboarding && (
        <Modal open={true} onClose={() => setShowOnboarding(false)} title="Welcome to KESI" type="info" wide={false}>
          <OnboardingModal onComplete={handleCompleteOnboarding} />
        </Modal>
      )}
      {showCaseDetails && selectedCase && (
        <Modal
          open={true}
          onClose={() => {
            setShowCaseDetails(false);
            setSelectedCase(null);
            handleSetCaseDetails(null);
          }}
          title={selectedCase.name}
          type="info"
          wide={true}
        >
          <div className="p-4">
            {/* Case Information */}
            <div className="mb-6">
              <h3 className="text-lg font-semibold mb-2">Case Information</h3>
              <div className="grid grid-cols-2 gap-4">
                <div>
                  <p className="text-sm text-gray-600">Case Number</p>
                  <p className="font-medium">{selectedCase.caseNumber || 'N/A'}</p>
                </div>
                <div>
                  <p className="text-sm text-gray-600">Status</p>
                  <p className="font-medium">{selectedCase.status || 'Active'}</p>
                </div>
                <div>
                  <p className="text-sm text-gray-600">Next Hearing</p>
                  <p className="font-medium">{selectedCase.nextHearing || 'Not scheduled'}</p>
                </div>
                <div>
                  <p className="text-sm text-gray-600">Your Role</p>
                  <p className="font-medium">{selectedCase.role || 'Participant'}</p>
                </div>
              </div>
            </div>

            {/* Tabs */}
            <div className="mb-6">
              <div className="flex space-x-4 border-b">
                <button
                  className={`px-4 py-2 font-medium ${
                    activeTab === 'timeline'
                      ? 'text-blue-600 border-b-2 border-blue-600'
                      : 'text-gray-500'
                  }`}
                  onClick={() => setActiveTab('timeline')}
                >
                  Timeline
                </button>
                <button
                  className={`px-4 py-2 font-medium ${
                    activeTab === 'documents'
                      ? 'text-blue-600 border-b-2 border-blue-600'
                      : 'text-gray-500'
                  }`}
                  onClick={() => setActiveTab('documents')}
                >
                  Documents
                </button>
                <button
                  className={`px-4 py-2 font-medium ${
                    activeTab === 'interactions'
                      ? 'text-blue-600 border-b-2 border-blue-600'
                      : 'text-gray-500'
                  }`}
                  onClick={() => setActiveTab('interactions')}
                >
                  Interactions
                </button>
                <button
                  className={`px-4 py-2 font-medium ${
                    activeTab === 'settlement'
                      ? 'text-blue-600 border-b-2 border-blue-600'
                      : 'text-gray-500'
                  }`}
                  onClick={() => setActiveTab('settlement')}
                >
                  Settlement
                </button>
              </div>

              {/* Timeline Tab */}
              {activeTab === 'timeline' && (
                <div className="mt-4 space-y-4">
                  {selectedCase.timeline?.map((event, index) => (
                    <motion.div
                      key={index}
                      initial={{ opacity: 0, x: -20 }}
                      animate={{ opacity: 1, x: 0 }}
                      className="flex items-start space-x-4 p-4 bg-white rounded-lg shadow"
                    >
                      <div className="flex-shrink-0 w-8 h-8 rounded-full bg-blue-100 flex items-center justify-center">
                        {event.icon}
                        </div>
                      <div className="flex-grow">
                        <h4 className="font-medium">{event.title}</h4>
                        <p className="text-sm text-gray-600">{event.description}</p>
                        <div className="mt-2 text-xs text-gray-500">{event.date}</div>
                      </div>
                    </motion.div>
                    ))}
                </div>
              )}

              {/* Documents Tab */}
              {activeTab === 'documents' && (
                <div className="mt-4 space-y-4">
                  <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                    {selectedCase.documents?.map((doc, index) => (
                      <motion.div
                        key={index}
                        initial={{ opacity: 0, y: 20 }}
                        animate={{ opacity: 1, y: 0 }}
                        className="p-4 bg-white rounded-lg shadow"
                      >
                        <div className="flex items-center space-x-3">
                          <div className="text-2xl">{doc.icon}</div>
                          <div>
                            <h4 className="font-medium">{doc.name}</h4>
                            <p className="text-sm text-gray-600">{doc.description}</p>
                          </div>
                        </div>
                        <div className="mt-3 flex justify-end">
                          <button className="text-blue-600 hover:text-blue-800 text-sm">View Document</button>
                      </div>
                      </motion.div>
                    ))}
                  </div>
                </div>
              )}

              {/* Interactions Tab */}
              {activeTab === 'interactions' && (
                <div className="mt-4 space-y-4">
                  <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                    {selectedCase.participants?.map((participant, index) => (
                      <motion.div
                        key={index}
                        initial={{ opacity: 0, y: 20 }}
                        animate={{ opacity: 1, y: 0 }}
                        className="p-4 bg-white rounded-lg shadow"
                      >
                        <div className="flex items-center space-x-3">
                          <div className="w-10 h-10 rounded-full bg-gray-200 flex items-center justify-center">
                            {participant.avatar || ''}
                          </div>
                          <div>
                            <h4 className="font-medium">{participant.name}</h4>
                            <p className="text-sm text-gray-600">{participant.role}</p>
                          </div>
                        </div>
                        <div className="mt-3 flex justify-end space-x-2">
                          <button
                            className="text-blue-600 hover:text-blue-800 text-sm"
                            onClick={() => {
                              setSelectedParticipant(participant);
                              setShowChatModal(true);
                            }}
                          >
                            Chat
                          </button>
                          <button className="text-green-600 hover:text-green-800 text-sm">Schedule Meeting</button>
                  </div>
                      </motion.div>
                    ))}
                  </div>
                </div>
              )}

              {/* Settlement Tab */}
              {activeTab === 'settlement' && (
                <div className="mt-4 space-y-4">
                  <div className="bg-white rounded-lg shadow p-4">
                    <div className="flex justify-between items-center mb-4">
                      <h4 className="font-medium">Settlement Negotiations</h4>
                      <button
                        className="bg-blue-600 text-white px-4 py-2 rounded hover:bg-blue-700"
                        onClick={() => setShowSettlementModal(true)}
                      >
                        Propose Settlement
                      </button>
                    </div>
                    <div className="space-y-4">
                      {selectedCase.settlements?.map((settlement, index) => (
                        <div key={index} className="p-3 bg-gray-50 rounded">
                          <div className="flex justify-between">
                            <span className="font-medium">{settlement.type}</span>
                            <span className="text-sm text-gray-500">{settlement.date}</span>
                          </div>
                          <p className="text-sm text-gray-600 mt-1">{settlement.terms}</p>
                          <div className="mt-2 text-xs text-gray-500">
                            Status: <span className="font-medium">{settlement.status}</span>
                          </div>
                        </div>
                      ))}
                    </div>
                  </div>
                  </div>
                )}
            </div>

            {/* Action Buttons */}
            <div className="flex justify-between items-center mt-6">
              <div className="flex space-x-2">
                <button
                  className="bg-blue-600 text-white px-4 py-2 rounded hover:bg-blue-700"
                  onClick={() => setShowRoleChangeModal(true)}
                >
                  Change Role
                </button>
                <button
                  className="bg-green-600 text-white px-4 py-2 rounded hover:bg-green-700"
                  onClick={() => handleSetShowCourtroom(true)}
                >
                  Enter Courtroom
                </button>
              </div>
              <button
                className="bg-red-600 text-white px-4 py-2 rounded hover:bg-red-700"
                onClick={() => setShowCaseDetails(false)}
              >
                Close
              </button>
            </div>
          </div>
        </Modal>
      )}
      {/* Profile & Progress */}
      <div className="bg-white rounded shadow p-4 flex flex-col md:flex-row gap-6 items-center">
        <div className="relative">
          <img src={profile.avatar} alt="avatar" className="w-20 h-20 rounded-full border-4 border-blue-100" />
          <button
            className="absolute bottom-0 right-0 bg-blue-600 text-white rounded-full p-1 shadow hover:bg-blue-700"
            title="Edit Profile"
            onClick={() => setShowEditProfileModal(true)}
            style={{ fontSize: 16 }}
          >
            
          </button>
        </div>
        <div className="flex-1">
          <div className="text-xl font-bold text-gray-800">{profile.name}</div>
          <div className="text-sm text-gray-500 mb-2">Role: {profile.role}</div>
          <div className="flex gap-4 mb-2 items-center">
            <div>Ongoing Cases: <span className="inline-block bg-blue-600 text-white rounded-full px-3 py-1 text-xs font-semibold ml-1">{activeCases.length}</span></div>
            <div>Completed: <span className="inline-block bg-green-600 text-white rounded-full px-3 py-1 text-xs font-semibold ml-1">3</span></div>
          </div>
          {/* Styled Skills Card */}
          <div className="mb-2">Skill Improvement:</div>
          <div className="flex flex-wrap gap-2 mb-2">
            {skills.map(skill => (
              <div key={skill.name} className="bg-blue-50 border border-blue-200 rounded-lg px-3 py-2 flex flex-col items-center w-32 shadow-sm">
                <span className="text-xs text-blue-700 font-semibold mb-1">{skill.name}</span>
                <div className="w-full bg-gray-200 rounded h-2 mb-1">
                  <div className="bg-blue-600 h-2 rounded" style={{ width: skill.value + '%' }}></div>
                </div>
                <span className="text-xs text-gray-700">{skill.value}%</span>
              </div>
            ))}
          </div>
          <div className="mt-2 flex gap-2 flex-wrap">
            {badges.map(b => (
              <span key={b.name} className="inline-flex items-center gap-1 bg-blue-50 text-blue-700 px-2 py-1 rounded text-xs font-semibold">{b.icon} {b.name}</span>
            ))}
          </div>
        </div>
      </div>
      {/* Active Cases with Avatars - styled card */}
      <div className="bg-white rounded-2xl shadow-lg p-6 mb-4 border border-blue-100">
        <div className="font-bold mb-4 text-lg text-blue-800 flex items-center gap-2">
          <span className="text-2xl"></span> Active Cases
        </div>
        <ul className="space-y-3">
          {activeCases.map(c => (
            <li 
              key={c.id} 
              className="flex items-center justify-between bg-blue-50 rounded-xl px-4 py-3 shadow-sm border border-blue-200 hover:shadow-md transition-all cursor-pointer" 
              onClick={() => {
                setSelectedCase(c);
                setShowCaseDetails(true);
                handleSetCaseDetails(c);
              }}
            >
              <div className="flex items-center gap-3">
                {/* Case Avatar */}
                <div style={{
                  width: 36,
                  height: 36,
                  borderRadius: '50%',
                  background: '#4f46e5',
                  color: '#fff',
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'center',
                  fontWeight: 'bold',
                  fontSize: 18,
                  boxShadow: '0 1px 4px rgba(0,0,0,0.08)'
                }}>{c.name.charAt(0)}</div>
                <div>
                  <div className="font-semibold text-blue-700 text-base">{c.name}</div>
                  <div className="text-xs text-gray-500">{c.type}  {c.area}  {c.location}</div>
                  {c.storyline && <div className="text-xs text-gray-600 mt-1 italic">{c.storyline}</div>}
                </div>
              </div>
              <div className="text-xs text-gray-500 text-right">
                <div>Role: <span className="font-semibold text-blue-700">{c.role}</span></div>
                <div>Deadline: {c.deadline}</div>
              </div>
            </li>
          ))}
        </ul>
      </div>
      {/* Uploaded Cases with Evidence */}
      <div className="bg-white rounded-2xl shadow-lg p-6 border border-blue-100">
        <div className="font-bold mb-4 text-lg text-blue-800 flex items-center gap-2">
          <span className="text-2xl"></span> Uploaded Case Files
        </div>
        <ul className="space-y-3">
          {uploadedCases.map(c => (
            <li key={c.id} className="bg-blue-50 rounded-xl px-4 py-3 shadow-sm border border-blue-200 hover:shadow-md transition-all">
              <div className="flex items-center gap-3 mb-2">
                <div style={{
                  width: 36,
                  height: 36,
                  borderRadius: '50%',
                  background: '#6366f1',
                  color: '#fff',
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'center',
                  fontWeight: 'bold',
                  fontSize: 18,
                  boxShadow: '0 1px 4px rgba(0,0,0,0.08)'
                }}>{c.name.charAt(0)}</div>
                <div>
                  <div className="font-semibold text-blue-700 text-base">{c.name}</div>
                  <div className="text-xs text-gray-500">{c.type}  Evidence: {c.evidence}</div>
                </div>
              </div>
              <div className="flex flex-wrap gap-3 mb-2">
                {(evidence[c.id] || []).map(ev => (
                  <div key={ev.id} className="bg-white border rounded p-2 flex flex-col items-center w-32 shadow-sm">
                    <div className="font-medium text-gray-800 text-xs mb-1">{ev.name}</div>
                    {ev.type === 'image' && <img src={ev.url} alt={ev.name} className="w-20 h-14 object-cover rounded mb-1" />}
                    {ev.type === 'video' && <video src={ev.url} controls className="w-20 h-14 rounded mb-1" />}
                    {ev.type === 'document' && <img src={ev.url} alt={ev.name} className="w-20 h-14 object-cover rounded mb-1" />}
                    {ev.type === 'text' && <div className="text-xs text-gray-600 italic">{ev.text}</div>}
                  </div>
                ))}
              </div>
              <form className="flex items-center gap-2" onSubmit={e => {
                e.preventDefault();
                const file = e.target.elements.evidence.files[0];
                const tags = e.target.elements.tags.value.split(',').map(t => t.trim()).filter(Boolean);
                const relevance = e.target.elements.relevance.value;
                handleUploadEvidence(c.id, file, tags, relevance);
                e.target.reset();
              }}>
                <input type="file" name="evidence" className="text-xs" accept="image/*,video/*,.pdf,.doc,.docx,.txt" />
                <input type="text" name="tags" placeholder="tags (comma separated)" className="text-xs border rounded px-1" />
                <select name="relevance" className="text-xs border rounded px-1">
                  <option value="high">High</option>
                  <option value="medium">Medium</option>
                  <option value="low">Low</option>
                </select>
                <button type="submit" className="bg-blue-600 text-white px-2 py-1 rounded text-xs hover:bg-blue-700">Upload Evidence</button>
              </form>
            </li>
          ))}
        </ul>
      </div>
      {showEditProfileModal && (
        <Modal open={true} onClose={() => setShowEditProfileModal(false)} title="Edit Profile" type="info" wide={false}>
          <EditProfileModal profile={profile} onSave={handleSaveProfile} onClose={() => setShowEditProfileModal(false)} />
        </Modal>
      )}
      {/* Role Change Modal */}
      <AnimatePresence>
        {showRoleChangeModal && (
          <motion.div
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
            className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4 z-50"
            onClick={() => setShowRoleChangeModal(false)}
          >
            <motion.div
              initial={{ scale: 0.9, opacity: 0 }}
              animate={{ scale: 1, opacity: 1 }}
              exit={{ scale: 0.9, opacity: 0 }}
              className="bg-white rounded-xl shadow-2xl p-6 w-full max-w-md"
              onClick={e => e.stopPropagation()}
            >
              <h3 className="text-xl font-bold mb-4">Change Role</h3>
              <select
                className="w-full p-2 border rounded-lg mb-4"
                value={newRole}
                onChange={(e) => setNewRole(e.target.value)}
              >
                <option value="">Select a new role</option>
                {getAvailableRoles(selectedCase, profile.role).map(role => (
                  <option key={role} value={role}>{role}</option>
                ))}
              </select>
              <div className="flex justify-end gap-2">
                <button
                  className="bg-gray-200 text-gray-800 px-4 py-2 rounded-lg hover:bg-gray-300"
                  onClick={() => setShowRoleChangeModal(false)}
                >
                  Cancel
                </button>
                <button
                  className="bg-blue-600 text-white px-4 py-2 rounded-lg hover:bg-blue-700"
                  onClick={handleRoleChange}
                >
                  Change Role
                </button>
              </div>
            </motion.div>
          </motion.div>
        )}
      </AnimatePresence>

      {/* Settlement Modal */}
      {showSettlementModal && (
        <Modal
          open={true}
          onClose={() => setShowSettlementModal(false)}
          title="Propose Settlement"
          type="info"
          wide={false}
        >
          <div className="p-4">
            <div className="mb-4">
              <label className="block text-sm font-medium text-gray-700 mb-1">Settlement Type</label>
              <select
                className="w-full border border-gray-300 rounded px-3 py-2"
                value={settlementType}
                onChange={(e) => setSettlementType(e.target.value)}
              >
                <option value="mediation">Mediation</option>
                <option value="direct">Direct Settlement</option>
                <option value="arbitration">Arbitration</option>
              </select>
            </div>
            <div className="mb-4">
              <label className="block text-sm font-medium text-gray-700 mb-1">Settlement Terms</label>
              <textarea
                className="w-full border border-gray-300 rounded px-3 py-2"
                rows="4"
                value={settlementTerms}
                onChange={(e) => setSettlementTerms(e.target.value)}
                placeholder="Enter the terms of the settlement..."
              />
            </div>
            <div className="mb-4">
              <label className="block text-sm font-medium text-gray-700 mb-1">Proposed Amount</label>
              <input
                type="text"
                className="w-full border border-gray-300 rounded px-3 py-2"
                value={settlementAmount}
                onChange={(e) => setSettlementAmount(e.target.value)}
                placeholder="Enter the proposed amount..."
              />
            </div>
            <div className="mb-4">
              <label className="block text-sm font-medium text-gray-700 mb-1">Deadline</label>
              <input
                type="date"
                className="w-full border border-gray-300 rounded px-3 py-2"
                value={settlementDeadline}
                onChange={(e) => setSettlementDeadline(e.target.value)}
              />
            </div>
            <div className="flex justify-end space-x-2">
              <button
                className="bg-blue-600 text-white px-4 py-2 rounded hover:bg-blue-700"
                onClick={() => {
                  // Handle settlement proposal
                  const settlement = {
                    type: settlementType,
                    terms: settlementTerms,
                    amount: settlementAmount,
                    deadline: settlementDeadline,
                    status: 'Pending',
                    date: new Date().toISOString(),
                  };
                  onAddInteraction(selectedCase.id, {
                    type: 'settlement',
                    content: settlement,
                  });
                  setShowSettlementModal(false);
                  showToast('Settlement proposal sent successfully!');
                }}
              >
                Propose Settlement
              </button>
              <button
                className="bg-gray-300 text-gray-700 px-4 py-2 rounded hover:bg-gray-400"
                onClick={() => setShowSettlementModal(false)}
              >
                Cancel
              </button>
            </div>
          </div>
        </Modal>
      )}

      {/* Chat Modal */}
      {showChatModal && selectedParticipant && (
        <Modal
          open={true}
          onClose={() => setShowChatModal(false)}
          title={`Chat with ${selectedParticipant.name}`}
          type="info"
          wide={false}
        >
          <div className="p-4">
            <div className="h-64 overflow-y-auto mb-4 border rounded p-2">
              {selectedCase?.chatHistory?.[selectedParticipant.id]?.map((message, index) => (
                <div
                  key={index}
                  className={`mb-2 p-2 rounded ${
                    message.sender === profile.id
                      ? 'bg-blue-100 ml-auto'
                      : 'bg-gray-100'
                  } max-w-[80%]`}
                >
                  <div className="text-xs text-gray-500 mb-1">
                    {message.sender === profile.id ? 'You' : selectedParticipant.name}
                  </div>
                  <div>{message.content}</div>
                </div>
              ))}
            </div>
            <div className="flex space-x-2">
              <input
                type="text"
                className="flex-grow border border-gray-300 rounded px-3 py-2"
                value={chatMessage}
                onChange={(e) => setChatMessage(e.target.value)}
                placeholder="Type your message..."
                onKeyPress={(e) => {
                  if (e.key === 'Enter' && chatMessage.trim()) {
                    onAddInteraction(selectedCase.id, {
                      type: 'chat',
                      content: chatMessage,
                      recipient: selectedParticipant.id,
                    });
                    setChatMessage('');
                  }
                }}
              />
              <button
                className="bg-blue-600 text-white px-4 py-2 rounded hover:bg-blue-700"
                onClick={() => {
                  if (chatMessage.trim()) {
                    onAddInteraction(selectedCase.id, {
                      type: 'chat',
                      content: chatMessage,
                      recipient: selectedParticipant.id,
                    });
                    setChatMessage('');
                  }
                }}
              >
                Send
              </button>
            </div>
          </div>
        </Modal>
      )}
    </div>
  );
} 