import React, { useState } from 'react';
import Modal from '../Modal';
import NewCaseModal from '../NewCaseModal';

export default function CasesSection({ 
  activeCases, 
  uploadedCases, 
  setShowNewCaseModal, 
  showNewCaseModal, 
  handleCreateCase,
  historicalCases,
  isLoadingCases
}) {
  return (
    <div className="p-4 space-y-6">
      {/* Active Cases */}
      <div className="bg-white rounded shadow p-4">
        <div className="flex justify-between items-center mb-4">
          <h2 className="text-xl font-bold">Active Cases</h2>
          <button
            className="bg-blue-600 text-white px-4 py-2 rounded hover:bg-blue-700"
            onClick={() => setShowNewCaseModal(true)}
          >
            New Case
          </button>
        </div>
        <div className="space-y-4">
          {activeCases.map((caseItem) => (
            <div key={caseItem.id} className="border rounded p-4">
              <h3 className="font-semibold">{caseItem.name}</h3>
              <p className="text-sm text-gray-600">{caseItem.type}  {caseItem.area}</p>
            </div>
          ))}
        </div>
      </div>

      {/* Historical Cases */}
      <div className="bg-white rounded shadow p-4">
        <h2 className="text-xl font-bold mb-4">Historical Cases</h2>
        {isLoadingCases ? (
          <div className="flex items-center justify-center p-4">
            <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-600"></div>
            <span className="ml-2">Loading cases...</span>
          </div>
        ) : (
          <div className="space-y-4">
            {historicalCases.map((caseItem, index) => (
              <div key={index} className="border rounded p-4">
                <h3 className="font-semibold">{caseItem.name}</h3>
                <p className="text-sm text-gray-600">{caseItem.area}  {caseItem.location}</p>
                {caseItem.storyline && (
                  <p className="text-sm text-gray-500 mt-2">{caseItem.storyline}</p>
                )}
              </div>
            ))}
          </div>
        )}
      </div>

      {/* Uploaded Cases */}
      <div className="bg-white rounded shadow p-4">
        <div className="font-bold mb-2">Uploaded Case Files</div>
        <ul>
          {uploadedCases.map(c => (
            <li key={c.id} className="flex items-center justify-between border-b py-2">
              <span>{c.name}</span>
              <span className="text-xs text-gray-500">Evidence: {c.evidence}</span>
            </li>
          ))}
        </ul>
      </div>

      {/* New Case Modal */}
      {showNewCaseModal && (
        <Modal
          open={true}
          onClose={() => setShowNewCaseModal(false)}
          title="Create New Case"
          type="info"
          wide={true}
        >
          <NewCaseModal
            onCreate={handleCreateCase}
            onClose={() => setShowNewCaseModal(false)}
            historicalCases={historicalCases}
          />
        </Modal>
      )}
    </div>
  );
} 